.todo{
    width: 600px;
    min-height: 732px;
    border-radius:20px;
    background:white ;
    margin: auto;
    margin-top: 174px;
    display: flex;
    /* flex-wrap: wrap; */
    flex-direction: column;
    padding: 0px 44px ;
    padding-bottom: 30px;
    margin-bottom: 100px;
}

.todo-header{
    margin-top: 84px;
    color: #002765;
    font-size: 46px;
    font-weight: 600;
    line-height: 34px;
    display: flex;
    justify-content: center;
}

.todo-add{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 45px;
}

.todo-input{
    border-radius: 50px;
    background-color: #edeef0;
    border: none;
    outline: none;
    width: 576px;
    height: 80px;
    padding-left:35px; 
    font-size: 20px;
}

.todo-add-btn{
    border-radius: 50px;
    background-color: #ff6739;
    width: 187px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: -100px;
    color: white;
    font-size: 26px;
    font-weight: 600;
    cursor: pointer;
}